<template>
  <div class="detial">
  	<div class="detail_box">
  		<!-- <Download></Download> -->
		<div  class="detail_imgbox" v-if="goodsDetail.picInfo">
			<mt-swipe :auto="5000">
			  <mt-swipe-item  v-for="(item,index) in goodsDetail.picInfo.picList"  v-bind:key="index" >
			  	<img v-bind:src="item+'@!1200'" alt="">
			  </mt-swipe-item>
			</mt-swipe>
			<p v-if="goodsDetail.basicInfo.onSession" ref='pdpa'>
				{{goodsDetail.sessionInfo.title}}<em class="triangle-left"></em><span ref='countDown'>距离结束：{{goodsDetail.sessionInfo.endTime|time}}</span>
			</p>
			<div id="backPrev" onclick="window.history.go(-1)"></div>
		</div>
		<div class="detail_desc" v-if="goodsDetail.basicInfo">
			<p class="detail_name" >{{goodsDetail.basicInfo.title}}</p>
			<p class="detail_price">
				<span>
					¥<em>{{goodsDetail.basicInfo.salePriceCent|capitalize}}</em>.{{goodsDetail.basicInfo.salePriceCent|capitalize2}}
				</span>
				<b>￥{{goodsDetail.basicInfo.originalPriceCent|transMoney00}}</b>
				<i>{{goodsDetail.basicInfo.discountPercent|transMoney0}}折</i>
			</p>
			<ul class="detail_discount" v-if='goodsDetail.basicInfo.onSession'>
				<li><span class="detail_discount_jian">减</span>每满{{goodsDetail.sessionInfo.discountThresholdCent|transMoney}}减{{goodsDetail.sessionInfo.discountCent|transMoney}},上不封顶</li>
			</ul>
			<ul class="detail_promise">
				<li>
					<span></span><em>100%正品</em>
				</li>
				<li>
					<span></span><em>7天无理由退货</em>
				</li>
				<li>
					<span></span><em>全场包邮</em>
				</li>
			</ul>
			
		</div>
		<div class="detail_botImg" v-if="goodsDetail.detailInfo">
			<img v-for="item in goodsDetail.detailInfo.picList" v-lazy="item+'@!1200'" alt="">
		</div>
		<div class="detail_bot_btn">
			<span @click="toCart">
				<img src="../assets/Bottom_label_icon_shopping_cart_onclick@3x.png" alt="">
			</span>
			<div id="addCart" @click="callUpBox('addCart')">
				加入购物车
			</div>
			<div id="buyNow" @click="callUpBox('buyNow')">
				立即购买
			</div>
		</div>
  	</div>
  	<transition name='fade'>
  		<div name="fade" v-if='states.callUpState' class="callUp_box" >
  			<div class="kongbai"></div>
			<div class="callUp" v-if="sKUDate">
				<h2><p>{{sKUDate.itemTitle}}</p><span id="callUp_close" @click="callUp_close"></span></h2>
				<div class="callUp_detail_img">
					<img v-bind:src="goodsDeatalInfo.pic+'@!400'" alt="">
					<div >
						<p>￥{{goodsDeatalInfo.salePriceCent|transMoney00}}</p>
						<p>库存：<em>{{goodsDeatalInfo.quantityLeft}}</em>件</p>
						<p>
							<span v-for="item in goodsDeatalInfo.attributeList">{{item.value}}</span>
						</p>
					</div>
				</div>
				<div class="skuScroll">
					<div class="callUp_style">
						<dl v-for='item in sKUDate.renderData'>
							<dt>{{item.name}}</dt>
							<dd>
								<span   v-for="(itit,index) in item.value"  :class='{active:item.iscur==index}' @click="changeSku(item,index,$event)">{{itit}}</span>
							</dd>
						</dl>
					</div>
					<dl class="callUp_count">
						<dt>
							数量
						</dt>
						<dd>
							<a  id="callUp_count_reduce" @click='reduceGoodsCount'></a>
							<em>{{goodsCount}}</em>
							<a  id="callUp_count_increase" @click="increaseGoodsCount"></a>
						</dd>
					</dl>
				</div>
				<div id="callUp_sure"  v-bind:data-num='goodsAllcount' @click="(addCart_info.num<=goodsAllcount)&&flag&&callUp_sure(states.isAddCart)" :class="{activebtn:flag&&(addCart_info.num<=goodsAllcount)}">
					确定
				</div>
			</div>
		</div >
  	</transition>
	
  </div>
</template>

<script>
import axios from 'axios'
import { Toast } from 'mint-ui';
export default {
  name: 'Detail',
  data () {
    return {
      states:{
      	callUpState:false,
      	isAddCart:'cart'
      },
      goodsDetail:{},
      time:0,
      sKUDateClass:[],
      sKUDate:{
      	itemTitle:'',
      	mainPic:'',
      	renderData:[],
      	library:[]
      },
      goodsCount:1,
      goodsAllcount:1,
      goodsDeatalInfo:[],
      addCart_info:{
      	skuId:null,
      	num:1
      },
      pre_order:undefined,
      flag:true
    }
  },
  created(){
  	let id = this.$route.query.id
  	this.getGoodsDetail(id);
  	this.getSKUList(id)
  },
  methods:{
  	//判断是加入购物车还是立即购买？并呼起callUp框
  	callUpBox(str){
  		this.states.callUpState = true;
  		document.getElementsByTagName('body')[0].style.overflow = 'hidden';
  		this.states.isAddCart = str;
  	},
  	callUp_sure(str){
  		//判断有没有登录
  		this.$http({
  			method:'get',
  			url:this.$api+'/h5/users/check_login'
  		}).then(res=>{
  			console.log(str)
  			
			if(str == 'addCart'){//加入购物车
	  			this.add_to_Cart_ajax(this.addCart_info);
	  		}else if(str == 'buyNow'){
	  			this.$set(this,'pre_order',[{
	  				'buyNum':this.addCart_info.num,
	  				'skuId':this.addCart_info.skuId
	  			}])
	  			this.$store.commit('newSubmit_order_data',this.pre_order);
	        	window.localStorage.submit_order_data = JSON.stringify(this.pre_order);
	        	this.$router.push({
		  			path:'/submit_order'
		  		})
	  		}
  		}).catch(error=>{//未登录状态
  			if(error.request.status == 401){
  				this.$store.commit('newRedirectUrl',location.href.split('#/')[1]);
		        window.localStorage.redirectUrl = location.href.split('#/')[1];
	            this.$router.push({
	              path:'./login'
	            })
	          }else{
	            let instance = Toast('网络好像有问题,请稍候再试试');
	            setTimeout(() => {
	              instance.close();
	            }, 2000);
	          }
	  		})
  			
  		
  	},
  	callUp_close(){
  		this.states.callUpState = false;
  		document.getElementsByTagName('body')[0].style.overflow = 'auto';
  	},
  	getGoodsDetail(id){
  		this.$http({
  			method:'get',
  			url:this.$api+'/h5/items/detail',
  			params:{
  				id:id
  			}
  		}).then(res=>{
  			this.goodsDetail = res.data.value;
  			this.time = res.data.value.basicInfo.onSession? ((new Date(res.data.value.sessionInfo.endTime.replace(/-/g,'/'))).getTime()-(new Date()).getTime()) : 0;
  			this.$nextTick(()=>{
  				// console.log(this.goodsDetail)
  				//数据更新之后倒计时
  				this.countDown(this.$refs.countDown);
  			})	
  		}).catch(error=>{
  				let instance = Toast('网络好像有问题,请稍候再试试');
				setTimeout(() => {
				  instance.close();
				}, 2000);
  			})
  	},
  	getSKUList(id){
  		this.$http({
  			method:'get',
  			url:this.$api+'/h5/items/sku_list',
  			params:{
  				itemId:id
  			}
  		}).then(res=>{
  			//先把sku首先要展示的信息展示出来
  			// console.log(res.data.value.skuList)
  			this.$set(this,'goodsDeatalInfo',res.data.value.skuList[0]);
  			this.$set(this,'goodsAllcount',res.data.value.skuList[0].quantityLeft);
  			//首次设置一下商品id	
  			this.$set(this.addCart_info,'skuId',res.data.value.skuList[0].id);
  			//判断库存
  			if(!res.data.value.skuList[0].quantityLeft){
  				this.$set(this,'flag',false)
  			}else{
  				this.$set(this,'flag',true)
  			}
  			// console.log(this.addCart_info)
  			this.sKUDate.library = res.data.value.skuList;
  			this.sKUDate.itemTitle = res.data.value.itemTitle;
  			this.sKUDate.mainPic = res.data.value.mainPic;
  			if(res.data.value.skuList){
  				res.data.value.skuList.forEach((item)=>{
  					// console.log(item.attributeList);
  					//第一步，先把所有的类型遍历出来
  					item.attributeList.forEach((itit)=>{
  						// console.log(itit)
  						//遍历存放的sku数据并判断，一个简单去重并push数据
  						//先把sKUDateClass放满
  						if(this.sKUDateClass.indexOf(itit.name)==-1){
  							this.sKUDateClass.push(itit.name)
  						}
  					})
  				})
  				//把name去重放到sKUData.renderData里面
  				this.sKUDateClass.forEach((item)=>{
  					this.sKUDate.renderData.push({
  						name:item,
  						iscur:0,
  						value:[]
  					})
  				})
  				//把value放到sKUData.renderData里面
  				res.data.value.skuList.forEach((item)=>{
  					
  					item.attributeList.forEach((itit)=>{
  						//判断name是不是对应,对应则放入value里面
  						this.sKUDate.renderData.forEach((it)=>{
  							if(it.name == itit.name && it.value.indexOf(itit.value)==-1){
  								// console.log(it.name);
  								it.value.push(itit.value)
  							}
  						})
  					})
  				})
  			}
  			// console.log(this.sKUDate.renderData);
  		}).catch(error=>{
			let instance = Toast('网络好像有问题,请稍候再试试');
			setTimeout(() => {
			  instance.close();
			}, 2000);
		})
  	},
  	reduceGoodsCount(){
  		this.goodsCount--;
  		if(this.goodsCount<=1){
  			this.goodsCount=1;
  		}
  		this.addCart_info.num = this.goodsCount;
  		// console.log(this.addCart_info)
  	},
  	increaseGoodsCount(){
  		this.goodsCount++;
  		if(this.goodsCount>=this.goodsAllcount){
  			this.goodsCount = this.goodsAllcount;
  		}
  		if(this.goodsAllcount<1){
  			this.goodsCount =1	
  		}
  		this.addCart_info.num = this.goodsCount;
  		// console.log(this.addCart_info)
  	},
  	changeSku(item,index,event){
  		//无效商品
  		if(event.target.className.indexOf('invalid')!=-1){
  			return false;
  		}else{
  			item.iscur = index;
  			let classArr = [];
	  		//获取带有active状态的标签
	  		this.$nextTick(()=>{
	  			let spanActive = document.getElementsByClassName('active');
		  		for(let i = 0;i<spanActive.length;i++){
		  			classArr.push({
		  				name:spanActive[i].parentNode.parentNode.firstChild.innerHTML,
		  				value:spanActive[i].innerHTML
		  			})
		  			// console.log(spanActive[i].parentNode.parentNode.firstChild.innerHTML)
		  		}
		  		//查询this.sKUDate.library字典
		  		// console.log(this.sKUDate.library)
		  		// console.log(classArr);
		  		// console.log(JSON.stringify(classArr))

		  		this.sKUDate.library.forEach((item)=>{
		  			// console.log(JSON.stringify(item.attributeList))
		  			//转成字符串比较
		  			
		  			// console.log(JSON.stringify(item.attributeList))
		  			if(JSON.stringify(classArr) == JSON.stringify(item.attributeList)){

		  				// console.log(JSON.stringify(item.attributeList))
		  				// console.log(item);
		  				// 设置物品的id	
		  				this.$set(this.addCart_info,'skuId',item.id);
		  				// console.log(this.addCart_info);
		  				//设置物品的详情
		  				this.$set(this,'goodsDeatalInfo',item);
		  				//修改库存总数
		  				this.$set(this,'goodsAllcount',item.quantityLeft);
		  				if(item.quantityLeft>0){
		  					this.$set(this,'flag',true)
		  				}else{
		  					this.$set(this,'flag',false)
		  				}
		  				//判断若是此时的this.goodsAllcount大于itit.quantityLeft(库存),让this.goodsAllcount为1
		  				if(this.goodsCount>item.quantityLeft){
		  					this.$set(this,'goodsCount',item.quantityLeft<1?1:item.quantityLeft);
		  				}
		  			}
		  			
		  		})
	  		})
  		}

  		
  	},
  	add_to_Cart_ajax(info){
  		console.log(111111)

  		let params = new FormData();
			params.append('skuId',info.skuId);
			params.append('num',info.num);
			console.log(params)
  		axios.post(this.$api+'/h5/cart/add',params).then((res)=>{
			 console.log(res);
  			if(res.status ==200 && res.data){
  				let instance = Toast('添加购物车成功');
				setTimeout(() => {
				  instance.close();
				  this.states.callUpState = false;
				  document.getElementsByTagName('body')[0].style.overflow = 'auto';
				}, 500);
  				
  			}
		})
		


  	},
  	countDown(dom){
  		if(dom){
  			setInterval(()=>{
	  			this.time -= 1000;
	  			let second = parseInt(this.time/1000)%60,
	    		min    = (parseInt(this.time/1000)-second)/60%60,
				hour   = ((parseInt(this.time/1000)-second)/60-min)/60%24,
				day    = (((parseInt(this.time/1000)-second)/60-min)/60-hour)/24;
				dom.innerHTML='距离结束：'+(day>0?day+'天':'')+(hour<10?'0'+hour:hour)+':'+(min<10?'0'+min:min)+':'+(second<10?'0'+second:second)
	  		},1000)
  		}
  	},
  	toCart(){
  		this.$http({
  			method:'get',
  			url:this.$api+'/h5/users/check_login'
  		}).then(res=>{
			this.$router.push({
				path:'./cart'
			})
  		}).catch(error=>{
          
          if(error.request.status == 401){
          	this.$store.commit('newRedirectUrl',location.href.split('#/')[1]);
		    window.localStorage.redirectUrl = location.href.split('#/')[1];
            this.$router.push({
              path:'./login'
            })
          }else{
            let instance = Toast('网络好像有问题,请稍候再试试');
            setTimeout(() => {
              instance.close();
            }, 2000);
          }

      })
  	}
  },
 filters: {
    capitalize (value) {
      if (!value) return '0'
      return parseFloat(value/100).toFixed(2).toString().split('.')[0]
    },
    capitalize2 (value) {
      if (!value) return '0'
      return parseFloat(value/100).toFixed(2).toString().split('.')[1]
    },
    
  },
  mounted(){
  	
  }
}
</script>

