			}
			span{
				width: 24px;
				height: 24px;
				background: url('../assets/detail_order_delete_icon@3x.png');
				background-size:100%; 
				cursor: pointer;
			}
		}
		.callUp_detail_img{
			display: flex;
			box-sizing:border-box;
			padding: 20px;
			>img{
				width: 90px;
				height: 90px;
			}
			div{
				flex:1;
				padding-left: 10px;
				line-height: 24px;
				p:nth-child(1){
					font-size:14px;
					color:#ff3766;
					letter-spacing:-1px;
					line-height:14px;
				}
				p:nth-child(2){
					font-size:12px;
					color:#333234;
					letter-spacing:0;
				}
				p:nth-child(3){
					line-height: 16px;
					margin-top: 33px;
					span{
						padding: 0 7px;
						transform:scale(0.9); 
						display: inline-block;
						border:1px solid #dee1e7;/*no*/
						border-radius: 3px;
					}
				}
			}
		}
		.skuScroll{
			max-height: 250px;
			overflow: scroll;
			.callUp_style{
				width: 335px;
				margin: 0 auto;
				border-top: 1px solid #f4f4f4;/*no*/
				border-bottom: 1px solid #f4f4f4;/*no*/
				padding: 10px 0;
				dt{
					font-size:14px;
					color:#333234;
					line-height: 24px;
				}
				dd{
					span{
						font-size:12px;
						color:#333234;
						display: inline-block;
						background:#f4f4f4;
						border-radius:26px;
						line-height: 25px;
						padding: 0 20px;
						margin:5px 15px 5px 0;
					}
					span.active{
						background: $pink;
						color: white;
					}
					span.invalid{
						color:#b2b2b2;
					}
				}
			}
			.callUp_count{
				width: 335px;
				padding: 10px 20px;
				dt{
					font-size:14px;
					color:#333234;
					line-height: 24px;	
				}
				dd{
					display: flex;
					align-items:center;
					a{
						display: inline-block;
						width: 20px;
						height: 20px;
						border-radius: 50%;
					}
					a#callUp_count_reduce{
						background: url('../assets/order_setting_subtract_normal_icon@3x.png');
						background-size:100%; 
					}
					a#callUp_count_increase{
						background: url('../assets/order_setting_add_unclickale_icon@3x.png');
						background-size:100%; 
					}
					em{
						font-family:PTSans-Caption;
						font-size:24px;
						color:#333234;
						line-height: 24px;
						margin: 0 10px;
					}
				}
			}
		}
		
		
		#callUp_sure{
			width: 100%;
			height: 48px;
			background: gray;
			line-height: 48px;
			text-align: center;
			font-size:18px;
			color:#ffffff;
		}
		.activebtn{
			background: $pink !important;
		}
	}
 }
 .fade-enter-active, .fade-leave-active {
  transition: opacity .5s
 }
 .fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
  opacity: 0
 }
</style>



